.player {
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.time-control {
  width: 70%;
  display: flex;
  align-items: center;

  input {
    width: 100%;
    -webkit-appearance: none;
    background: transparent;
    cursor: pointer;
  }

  p {
    padding: 1rem;
  }
}

.play-control {
  width: 70%;
  display: flex;;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  color: #ffff;

  svg {
    cursor: pointer;
  }
}


/* CUSTOMIZE INPUT RANGE */

input[type="range"]:focus {
  outline-style: none;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 16px;
  width: 16px;
}

input[type="range"]::-moz-range-thumb {
  -webkit-appearance: none;
  background: none;
  border: none;
}

.track {
  background: lightblue;
  width: 100%;
  height: 1rem;
  position: relative;
  border-radius: 1rem;

  // hide any thing beyond flow
  overflow: hidden;
}

// will go right on top of .tarck
.animate-track {
  background: rgb(204, 204, 204);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  // this will show as progress 
  transform: translateX(0%);
  // allow to grab the bar even it behind 
  pointer-events: none;
}

@media screen and (max-width: 768px) {
  .time-control {
    width: 90%;
  }

  .play-control {
    width: 60%;
  }
}