* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h2, p {
  color: #ffff;
}
h1,h3 {
  color: rgb(58, 58, 58);
}

body {
  font-family: 'Lato', sans-serif;
}
  
h3,
h4 {
  font-weight: 400;
  color: rgb(100, 100, 100);
}

.App {
  transition: all 0.5s ease;
}

.library-active {
  // the same used at .library width (_library.scss)
  margin-left: 20rem;
}
.display-container {
  display: flex;
  width: 100%;
  height: 100vh;
}

.outer-display  {
  position: absolute;
  // display: flex;
  width: 100%;
  height: 100vh;
  opacity: 0.5;
}

