.inner-display {
  position: relative;
  // left: 50%;
  // top: 50%;
  width: 40%;
  height: 80%;
  background-color: lightpink;
  margin: auto;
  min-width: 320px;
  max-width: 440px;
  border-radius: 1rem;
  background: linear-gradient(0deg, rgba(8,34,34,1) 0%, rgba(0,1,1,0.6727065826330532) 100%);
  box-shadow: 13px 31px 52px -19px rgba(0, 0, 0, 0.75);
}

@import "./library";
@import "./nav";
@import "./player";
@import "./song";

